/* Dropdown styling */

.dropdown-menu {
    position: absolute;
    z-index: 1000;
    min-width: 12rem;
    top: 3.5rem;
    right: 19px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    padding-left: 0;
    margin-bottom: 0;
    border-radius: .25rem;
    outline: 9999px solid rgba(0,0,0,.5); /* to provide overlay */
}

.dropdown-menu::after {
    content: "";
    position: absolute;
    bottom: 100%;
    left: 74%;
    margin-left: -5px;
    border: 9px solid;
    border-color: transparent transparent white transparent;
}

.dropdown-item {
    position: relative;
    display: block;
    padding: 12px 1rem;
    color: #565b5f;
    text-decoration: none;
    background-color: #fff;
    border-bottom: 1px solid rgba(0,0,0,.125);
    font-size: 12px;
    font-weight: 600;
    cursor:pointer;
}

/* Overriding Progress Bar styling */

progress[value] {
  -webkit-appearance: none;
  appearance: none;
}

progress[value]::-webkit-progress-bar {
  background-color : #eee;
  border-radius : 10px;
  box-shadow: 0 2px 5px rgba(0,0,0,0.25) inset;
}

progress[value]::-webkit-progress-value {
  background-image: 
    -webkit-linear-gradient(top, rgba(255,255,255,.25), 
                              white),
    -webkit-linear-gradient(left,  #63ABFF, #007FFF);

    border-radius: 10px;
    background-size: 35px 20px, 100% 100%, 100% 100%;
    
}

/* Overriding react-date-range picker */

.rdrDateRangePickerWrapper {
    height: 353px !important;
    border: 1px solid transparent !important;
    box-shadow: 1px 4px 11px #000 !important;
}

.rdrDefinedRangesWrapper {
  width: 140px !important;
}

.rdrCalendarWrapper {
    font-size: 11px !important;
    width: 546px !important;
}

